import { ElMessage } from "element-plus";

export default {
    // copy
    mounted(el, binding) {
        const contentText = binding.value || el.innerText;
        // 
        const handleCopy = () => {
            // Clipboard API 
            if (navigator.clipboard) {
                navigator.clipboard
                    .writeText(contentText)
                    .then(() => {
                        ElMessage.success("");
                    })
                    .catch(() => {
                        ElMessage.error("");
                    });
            } else {
                // Clipboard APIdocument.execCommand('copy')
                const textAreaEl = document.createElement("textarea");
                textAreaEl.value = contentText;
                document.body.appendChild(textAreaEl);
                textAreaEl.select();
                document.execCommand("copy"); //
                document.removeChild(textAreaEl); //
                ElMessage.success("");
            }
        };
        // 
        el._handelCopy = handleCopy;
        el.addEventListener("click", el._handelCopy);
    },
    unmounted(el) {
        // 
        el.removeEventListener("click", el._handelCopy);
    },
};
